.vrk {
-webkit-animation: VRK 1.5s 1 0s ease-out;
        animation: VRK 1.5s 1 0s ease-out;
}

@-webkit-keyframes VRK {
  0% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
  }

  50% {
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
  }

  100% {
    -webkit-transform: rotate(xx) translateX(0px);
            transform: rotate(xx) translateX(0px);
  }
}

@keyframes VRK {
  0% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
  }

  50% {
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
  }

  100% {
    -webkit-transform: rotate(xx) translateX(0px);
            transform: rotate(xx) translateX(0px);
  }
}
.App-header {
 -webkit-clip-path: polygon(0 0, 100% 0, 100% 64%, 0 84%);
 clip-path: polygon(0 0, 100% 0, 100% 64%, 0 84%);
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  color: #F1F7EE;
  padding-top: 20vh;   
}

h1 {
    font-size: calc(10px + 8vmin);
      font-family: Georgia,
      serif;
      -webkit-animation: scale 2.2s 1 0s ease-out;
              animation: scale 2.2s 1 0s ease-out;
}

h6 {
  -webkit-animation: yourAnimation 2s 1 0s ease-out;
          animation: yourAnimation 2s 1 0s ease-out;
}

.Header-text2 {
  color: #ED1C25;   
}

@-webkit-keyframes yourAnimation {
  0% {
    -webkit-transform: translateY(-400px);
            transform: translateY(-400px);
  }

  50% {
    -webkit-transform: translateY(100px) skewY(-18deg);
            transform: translateY(100px) skewY(-18deg);
  }

  100% {
    -webkit-transform: rotate(xx) translateY(0px);
            transform: rotate(xx) translateY(0px);
  }
}

@keyframes yourAnimation {
  0% {
    -webkit-transform: translateY(-400px);
            transform: translateY(-400px);
  }

  50% {
    -webkit-transform: translateY(100px) skewY(-18deg);
            transform: translateY(100px) skewY(-18deg);
  }

  100% {
    -webkit-transform: rotate(xx) translateY(0px);
            transform: rotate(xx) translateY(0px);
  }
}

@-webkit-keyframes scale {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
  }

  50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0.5;
  }

  100% {
    -webkit-transform: rotate(xx) scale(1);
            transform: rotate(xx) scale(1);
    opacity: 1;
  }
}

@keyframes scale {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
  }

  50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0.5;
  }

  100% {
    -webkit-transform: rotate(xx) scale(1);
            transform: rotate(xx) scale(1);
    opacity: 1;
  }
}

@media screen and (min-width:603px) {

  .App-header {
   
    padding-top: 30vh;
