.App-header {
 -webkit-clip-path: polygon(0 0, 100% 0, 100% 64%, 0 84%);
 clip-path: polygon(0 0, 100% 0, 100% 64%, 0 84%);
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  color: #F1F7EE;
  padding-top: 20vh;   
}

h1 {
    font-size: calc(10px + 8vmin);
      font-family: Georgia,
      serif;
      animation: scale 2.2s 1 0s ease-out;
}

h6 {
  animation: yourAnimation 2s 1 0s ease-out;
}

.Header-text2 {
  color: #ED1C25;   
}

@keyframes yourAnimation {
  0% {
    transform: translateY(-400px);
  }

  50% {
    transform: translateY(100px) skewY(-18deg);
  }

  100% {
    transform: rotate(xx) translateY(0px);
  }
}

@keyframes scale {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }

  50% {
    transform: scale(1.5);
    opacity: 0.5;
  }

  100% {
    transform: rotate(xx) scale(1);
    opacity: 1;
  }
}

@media screen and (min-width:603px) {

  .App-header {
   
    padding-top: 30vh;
    padding-left: 12vw;
  }
}